.max-center{
  max-width:100%;
  margin:auto;
}


body
{
  background-color: #141414;
  color:white;
  margin:20px;
}


.header-content{
  display:flex;
  justify-content: space-between;
  align-items:center;
  margin-bottom:20px
}

.container{
  margin:20px;
  display:grid;
  gap:10px;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  background-color:#0F1014;
}

